import Vue from "vue";
import Router from "vue-router";
import HelloWorld from "@/components/HelloWorld";
import graph from "@/components/graphCanvas/graph";
Vue.use(Router);

export default new Router({
    routes: [{
        path: "/",
        name: "graph",
        component: graph
    }]
});


